.App {
    margin: 0px;
  }
  
  .table-container {
    display: flex;
    margin-top: 0px;
  }
  
  .scrollable-table-container {
    width: 50%;
    height: 200px;
    overflow-y: auto;
    border: 1px solid #ddd;
  }
  
  .ant-table {
    table-layout: fixed; /* Ensure fixed table layout for proper alignment */
  }
  
  .custom-input {
    margin-left: 1px;
    width: 70px;
    text-align: center;
  }
  
  .keyboard {
    display: none; /* Hide by default */
    flex-wrap: wrap;
    justify-content: center;
    margin-top: -10px;
  }
  
  .keyboard button {
    width: 100px;
    height: 50px;
    margin: 5px;
  }
  
  @media (max-width: 768px) {
    .keyboard {
      display: flex; /* Show on mobile screens */
    }
    
  }
  @media (min-width: 768px) {
    .back {
      display: none; /* Show on mobile screens */
    }
    
  }
  .dark-theme .ant-table {
    background-color: #1e1e1e;
    color: #ffffff;
  }
  
  .dark-theme .ant-table-thead > tr > th {
    background-color: #2a2a2a;
    color: #ffffff;
  }
  
  .dark-theme .ant-table-tbody > tr > td {
    background-color: #1e1e1e;
    color: #ffffff;
  }
  
  .dark-theme .ant-table-bordered .ant-table-container {
    border: 1px solid #444444;
  }
  
  .dark-theme .ant-table-bordered .ant-table-cell {
    border-right: 1px solid #444444;
  }
  
  .dark-theme .ant-table-bordered .ant-table-thead > tr > th,
  .dark-theme .ant-table-bordered .ant-table-tbody > tr > td {
    border-bottom: 1px solid #444444;
  }
  
  .dark-theme .ant-table-tbody > tr:hover > td {
    background-color: #444444;
    color: #000000;
  }
  
  .dark-theme .ant-table-pagination {
    background-color: #1e1e1e;
    color: #ffffff;
  }
  
  .dark-theme .ant-pagination-item-active {
    border-color: #444444;
  }
  
  .dark-theme .ant-pagination-item-active a {
    color: #ffffff;
  }
  
  .dark-theme .ant-pagination-item a {
    color: #ffffff;
  }
  
  